"use client"

import type React from "react"
import { useState } from "react"

export default function Chat() {
  const [messages, setMessages] = useState<Message[]>([])
  const [input, setInput] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [useWebSearch, setUseWebSearch] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!input.trim()) return

    const userMessage: Message = { role: "user", content: input }
    setMessages((prev) => [...prev, userMessage])
    setInput("")
    setIsLoading(true)

    try {
      const response = await fetch("http://localhost:8000/query", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ 
                              query: userMessage.content, 
                              history: messages, 
                              use_web_search: useWebSearch 
                            })
      })
      if (!response.ok) throw new Error("Failed to get response")
      const data = await response.json()
      setMessages((prev) => [...prev, { role: "assistant", content: data.response || "Sorry, I couldn't process that request." }])
    } catch (err) {
      console.error("Error:", err)
      setMessages((prev) => [...prev, { role: "assistant", content: "Sorry, there was an error processing your request." }])
    } finally {
      setIsLoading(false)
    }
  }

  const isEmpty = messages.length === 0

  return (
    <div className="bg-neutral-100 dark:bg-black font-sans min-h-screen flex items-center justify-center">
      <div className={`flex flex-col w-full max-w-3xl p-4 ${isEmpty ? "justify-center h-full" : "h-screen"}`}>
        {isEmpty && (
          <p className="font-bold text-black dark:text-white text-3xl text-center py-10">What can I help you with?</p>
        )}
        <MessageList messages={messages} isLoading={isLoading} />
        <ChatInput
          input={input}
          setInput={setInput}
          isLoading={isLoading}
          useWebSearch={useWebSearch}
          setUseWebSearch={setUseWebSearch}
          onSubmit={handleSubmit}
        />
      </div>
    </div>
  )
}
